import React,{useState} from 'react';
import axios from 'axios';
import {Table,Row, Col, Button} from 'react-bootstrap'


export default class PersonList extends React.Component {
    state = {
        persons: []
    }


    updateList(){
        axios.get("http://localhost:8080/persons")
            .then(res => {
                const persons = res.data;
                console.log(persons);
                this.setState({ persons });
            })
    }

    removePerson(id){
        axios.delete("http://localhost:8080/persons",{id})
            .then(res => {
                this.updateList();
            });
    }

    componentDidMount() {
        axios.get("http://localhost:8080/persons")
            .then(res => {
                const persons = res.data;
                console.log(persons);
                this.setState({ persons });
            })
    }

    render() {

        return (
            <Row>
                <Col>
                    <Table Classname='striped bordered hover'>
                        <thead>
                        <tr>
                            <th>#</th>
                            <th>First Name</th>
                            <th>Last Name</th>
                            <th>age</th>
                            <th>  </th>
                        </tr>
                        </thead>
                        <tbody>
                        {
                            this.state.persons
                                .map(person =>
                                    <tr>
                                        <td key={person.id}>{person.id}</td>
                                        <td>{person.fname}</td>
                                        <td>{person.lname}</td>
                                        <td>{person.age}</td>
                                        <td>
                                            <Button variant="danger" type="submit" onClick={this.removePerson(person.id)}>
                                                Delete person
                                            </Button>
                                        </td>
                                    </tr>
                                )
                        }
                        </tbody>
                    </Table>
                </Col>
            </Row>
        )
    }
}